#login .header{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

#login .header>div:nth-child(1){
    background: url(data:image/png;base64,) no-repeat;
    width: 179px;
    height: 44px;
}

#login .content{
    position: absolute;
    top: 64PX;
    left: 0;
    right: 0;
    bottom: 32px;
}

#login .content>div:nth-child(1){
    background: url(/shop/static/media/bg.2b4305fd.jpg) no-repeat center center;
    background-size: cover;
    opacity: .4;
    width: 100%;
    height: 100%;
}

#login .footer{
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 0;
    height: 32px;
    background-color: #1F3668;
    line-height: 32px;
    text-align: center;
}

#login .content>div:nth-child(2){
    width:710px;
    height:470px;
    border-radius: 5px;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    display: flex;
    flex-direction: row;
    align-items: center;
}

#login .content>div:nth-child(2) .bg{
    width: 50%;
    height: 100%;
    background: url(/shop/static/media/login.bf3ff73a.png) no-repeat center center;
    background-size: cover;
}

#login .content>div:nth-child(2) .input{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    height: 100%;
    width: 50%;
}

#login .content>div:nth-child(2) .input>div{
    padding: 10px 50px;
    width: 100%;
    text-align: center;
}

#login .content>div:nth-child(2) .input>div>h2{
    color: #409EFF;
}
